import React, { Fragment } from 'react';
import Link from 'next/link';

const Navigation = () => {
  return (
    <Fragment>
      <ul>
        <Link href="/">
          <a>Home</a>
        </Link>
      </ul>
      <style jsx>{`
        ul {
          background-color: #74d9f2;
        }
        a {
          color: black;
        }

        `}</style>
    </Fragment>
  );
}

export default Navigation;
